.page-header__nav {
  @media (min-width: $width-tablet) {
    position: relative;
    display: grid;
    grid-template-columns: 174px 1fr;
    column-gap: 20px;
    width: $width-tablet-content;
    margin: 0 auto;
    padding: 24px 0;
  }

  @media (min-width: $width-pre-desktop) {
    width: $width-desktop-content;
    padding: 53px 0 29px;
  }
}

.page-header__nav--decorate {
  @media (max-width: $width-max-mobile) {
    border-bottom: 1px solid $special-grey-150;
  }
}

.page-header__bar {
  @media (max-width: $width-max-mobile) {
    display: grid;
    width: 320px;
    margin: 0 auto;
    grid-template-columns: 34px 1fr 34px;
    padding: 12px 20px 16px;
    align-items: center;
  }
}

.page-header__menu {
  margin: 0;
  padding: 0;

  @media (min-width: $width-tablet) {
    margin-right: -17px;
  }
}

.page-header__logo {

  //          2  -. 3-   [X]
  @media (max-width: $width-max-mobile) {
    grid-column: 1 / span 2;
  }

  //      
  @media (min-width: $width-tablet) {
    & .logo__name {
      display: none;
    }
  }
}

///     

//       [X] +3       .
//   ""      3       [X].
// ..  -: 18  + : 3  =   : 24 ,
//        

.page-header__toggler {
  grid-column: -2;
  justify-self: right;
  position: relative;
  width: 24px;
  height: 24px;
  padding: 0;
  border: none;

  @media (min-width: $width-tablet) {
    display: none;
  }
}

///    [X]

.page-header__toggler--close {
  &::before,
  &::after {
    position: absolute;
    top: 0;
    left: 11px;
    width: 1.5px;
    height: 25px;
    content: " ";
    background-color: $color-black;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}
