.promo {
  @media (max-width: $width-max-mobile) {
    color: $color-white;
    text-align: center;

    background: {
      image: url("../../img/backtile/promo-small.jpg"),
        linear-gradient($basic-green, $basic-green 302px, $color-white 302px);
      repeat: no-repeat;
      position: 50% 0;
    }
  }

  @media (min-width: $width-pre-desktop) {
    //   ,          .
    position: relative;
  }
}

.promo__wrapper {
  @media (max-width: $width-max-mobile) {
    padding-top: 27px;
  }

  @media (min-width: $width-tablet) {
    position: relative;
    padding: 38px 87px 526px;
  }

  @media (min-width: $width-pre-desktop) {
    //       .promo.
    position: initial;
    padding: 111px 726px 185px 80px;

    ///    .
    &::after {
      content: "";
      position: absolute;
      width: 50%;
      background-color: #68b738;
      //    (-150px),  top-       .
      //     140px,  -150px   .
      top: -150px;
      right: 0;
      bottom: 0;
      z-index: -2;
    }
  }
}

.promo__title {
  @media (max-width: $width-max-mobile) {
    font-size: 36px;
    margin-bottom: 41px;
  }

  @media (min-width: $width-tablet) {
    font-size: 60px;
    margin-bottom: 41px;
  }
}

.promo__subtitle {
  text-transform: uppercase;

  @media (max-width: $width-max-mobile) {
    margin-bottom: 29px;
  }

  @media (min-width: $width-tablet) {
    font-size: 20px;
    margin-bottom: 49px;
  }
}

.promo__image {
  display: block;
  margin: 0 auto;

  @media (min-width: $width-tablet) {
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: -1;
    width: 709px;
    height: 609px;
  }

  @media (min-width: $width-pre-desktop) {
    top: 29px;
    ///      >      .
    /// ,    ,             .
    left: calc(50% - 162px);
    width: 552px;
    height: 500px;
  }
}

.promo__button {
  @media (max-width: $width-max-mobile) {
    width: 100%;
    padding: 11px 26px;
  }

  @media (min-width: $width-tablet) {
    padding: 14px 26px;
    width: auto;
    font-size: 20px;
    line-height: 20px;
  }
}
