.fieldset {
  padding: 0;
  border: 0;
}

.fieldset__list {
  display: grid;
  margin: 0 auto;
  padding: 0;
  list-style-type: none;

  // @include outline(violet, 1px, dashed);

  @media (max-width: $width-max-mobile) {
    width: $width-mobile-content;
  }

  @media (min-width: $width-tablet) {
    width: $width-tablet-content;
    padding-right: $width-tablet-padding-inner;
    padding-left: $width-tablet-padding-inner;
  }

  @media (min-width: $width-pre-desktop) {
    width: $width-desktop-content;
    padding: 0;
  }
}

.fieldset__list--about {
  row-gap: 14px;

  // @media (max-width: $width-max-mobile) {}

  // @media (min-width: $width-tablet) {}

  @media (min-width: $width-pre-desktop) {
    //  width: auto, ..        2-       .
    width: auto;
  }
}

.fieldset__list--target {
  row-gap: 12px;

  // @media (max-width: $width-max-mobile) {}

  @media (min-width: $width-tablet) {
    //         .
    //        padding-.
    //          ,          .
    width: ($width-tablet-content - 2 * $width-tablet-padding-inner);
    padding: 25px 28px;
    row-gap: 13px;
    border: 1px solid $special-grey-200;
  }

  @media (min-width: $width-pre-desktop) {
    //  width: auto, ..        2-       .
    width: auto;
  }
}

.fieldset__list--contacts {
  row-gap: 14px;

  // @media (max-width: $width-max-mobile) {}

  // @media (min-width: $width-tablet) {}

  @media (min-width: $width-pre-desktop) {
    grid-template-columns: 1fr 1fr;
    column-gap: 80px;
  }
}

.fieldset__list--more {
  @media (max-width: $width-max-mobile) {
    row-gap: 12px;
  }

  @media (min-width: $width-tablet) {
    grid-template-columns: 1fr 1fr;
    column-gap: 60px;
    row-gap: 32px;
  }

  @media (min-width: $width-pre-desktop) {
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 80px;
  }
}

.fieldset__wrapper {
  width: 100%;
  padding: 0;

  &--contacts {
    @media (max-width: $width-max-mobile) {
      margin-bottom: 35px;
    }

    @media (min-width: $width-tablet) {
      margin-bottom: 76px;
    }

    // @media (min-width: $width-pre-desktop) {}
  }

  &--comments {
    @media (max-width: $width-max-mobile) {
      margin-bottom: 37px;
    }

    @media (min-width: $width-tablet) {
      margin-bottom: 76px;
    }

    // @media (min-width: $width-pre-desktop) {}
  }

  &--more {
    @media (max-width: $width-max-mobile) {
      margin-bottom: 35px;
    }

    @media (min-width: $width-tablet) {
      margin-bottom: 57px;
    }

    // @media (min-width: $width-pre-desktop) {}
  }
}
