/// 
$width-mobile: 320px;
$width-tablet: 768px;
$width-desktop: 1440px;

///    
$width-mobile-padding: 20px;
$width-tablet-padding: 30px;
$width-tablet-padding-inner: 40px;
$width-desktop-padding: 110px;

///   
$width-mobile-content: (
  $width-mobile - 2 * $width-mobile-padding
);
$width-tablet-content: (
  $width-tablet - 2 * $width-tablet-padding
);
$width-desktop-content: (
  $width-desktop - 2 * $width-desktop-padding
);
///       
$width-max-mobile: (
  $width-tablet - 1
);
///         (..    )
$width-pre-desktop: (
  $width-desktop-content + 100px
);


/// 
$font-title: "Oswald", "Arial", sans-serif;
$font-body: "Arial", sans-serif;

/// 
$color-black: #000000;
$color-white: #ffffff;
$basic-green: #68b738;
$basic-grey: #f2f2f2;
$secondary-green: #5eaa2f;
$special-grey-100: #ebebeb;
$special-grey-150: #e6e6e6;
$special-grey-200: #d9d9d9;
$special-grey-250: #cdcdcd;
$special-grey-600: #666666;
$special-grey-700: #444444;
$special-grey-800: #222222;
$special-grey-900: #111111;
